import React from 'react';
import Img from './card_img.svg';
// import Background from './background.svg';
class Modal extends React.Component{

    constructor( props ){
        super( props );

        this.state={
            d1 : false,
            d2 : false,
            d3 : false,
            d4 : false,
            d5 : false,
            d6 : false,
            d7 : false,
            d8 : false,
            d9 : false,
            d10 : false,
            d11 : false,
            d12 : false,
            d13 : false,
            d14 : false,
            d15 : false,
            d16 : false,
            d17: false,
            d18 : false,
            d19 : false,
            d20 : false,
            d21 : false,
            d22 : false,
            d23 : false,
            d24 : false,
            d25 : false,
            d26 : false,
            d27 : false,
            d28 : false,
            d29 : false,
            d30 : false,
            d31 :false,
            selected : 0
        }

        this.handleClick = this.handleClick.bind(this);
        this.checkState = this.checkState.bind(this);
      }
    handleClick(id){
    
        for(var i=1;i<=31;i++)
        {
            if(id===1)
            this.setState({
                d1 : !this.state.d1
            })
    
            if(id===2)
            this.setState({
                d2 : !this.state.d2
            })
    
            if(id===3)
            this.setState({
                d3 : !this.state.d3
            })
    
            if(id===4)
            this.setState({
                d4 : !this.state.d4
            })
    
            if(id===5)
            this.setState({
                d5 : !this.state.d5
            })
    
            if(id===6)
            this.setState({
                d6 : !this.state.d6
            })
    
            if(id===6)
            this.setState({
                d6 : !this.state.d6
            })
    
            if(id===7)
            this.setState({
                d7 : !this.state.d7
            })
    
            if(id===8)
            this.setState({
                d8 : !this.state.d8
            })
    
            if(id===9)
            this.setState({
                d9 : !this.state.d9
            })
    
            if(id===10)
            this.setState({
                d10 : !this.state.d10
            })
    
            if(id===11)
            this.setState({
                d11 : !this.state.d12
            })
    
            if(id===12)
            this.setState({
                d12 : !this.state.d12
            })
    
            if(id===13)
            this.setState({
                d13 : !this.state.d13
            })
    
            if(id===14)
            this.setState({
                d14 : !this.state.d14
            })
    
            if(id===15)
            this.setState({
                d15 : !this.state.d15
            })
    
            if(id===16)
            this.setState({
                d16 : !this.state.d16
            })
    
            if(id===17)
            this.setState({
                d17 : !this.state.d17
            })
    
            if(id===18)
            this.setState({
                d18 : !this.state.d18
            })
    
            if(id===19)
            this.setState({
                d19 : !this.state.d19
            })
    
            if(id===20)
            this.setState({
                d20 : !this.state.d20
            })
    
            if(id===21)
            this.setState({
                d21: !this.state.d21
            })
    
            if(id===22)
            this.setState({
                d22 : !this.state.d23
            })
    
            if(id===23)
            this.setState({
                d23 : !this.state.d23
            })
    
            if(id===24)
            this.setState({
                d24 : !this.state.d24
            })
    
            if(id===25)
            this.setState({
                d25 : !this.state.d25
            })
    
            if(id===26)
            this.setState({
                d26 : !this.state.d26
            })
    
            if(id===27)
            this.setState({
                d27 : !this.state.d27
            })
    
            if(id===28)
            this.setState({
                d28 : !this.state.d28
            })
    
            if(id===29)
            this.setState({
                d29 : !this.state.d29
            })
    
            if(id===30)
            this.setState({
                d30 : !this.state.d30
            })
    
            if(id===31)
            this.setState({
                d31 : !this.state.d31
            })
        }
    }

    checkState(id){

        if(id===1)
        return this.state.d1;

        if(id===2)
        return this.state.d2;

        if(id===3)
        return this.state.d3;

        if(id===4)
        return this.state.d4;

        if(id===5)
        return this.state.d5;

        if(id===6)
        return this.state.d6;

        if(id===7)
        return this.state.d7;

        if(id===8)
        return this.state.d8;

        if(id===9)
        return this.state.d9;

        if(id===10)
        return this.state.d10;

        if(id===11)
        return this.state.d11;

        if(id===12)
        return this.state.d12;

        if(id===13)
        return this.state.d13;

        if(id===14)
        return this.state.d14;

        if(id===15)
        return this.state.d15;

        if(id===16)
        return this.state.d16;

        if(id===17)
        return this.state.d17;

        if(id===18)
        return this.state.d18;

        if(id===19)
        return this.state.d19;

        if(id===20)
        return this.state.d20;

        if(id===21)
        return this.state.d21;

        if(id===22)
        return this.state.d22;

        if(id===23)
        return this.state.d23;

        if(id===24)
        return this.state.d24;

        if(id===25)
        return this.state.d25;

        if(id===26)
        return this.state.d26;

        if(id===27)
        return this.state.d27;

        if(id===28)
        return this.state.d28;

        if(id===29)
        return this.state.d29;

        if(id===30)
        return this.state.d30;

        if(id === 31)
        return this.state.d31;
    }

    handleSchedule(id){
        if(this.state.selected!==id)
        this.setState({selected : id})
        else
        this.setState({selected : id})
    }

    render(){

    var date = new Date();
    var months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    var weekday = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
    var dayOfWeek = date.getDay(); 
    var curMonth = months[date.getMonth()];
    var curYear = date.getFullYear();
    var days = date.getDate();
    var total = new Date(curYear,date.getMonth(),0).getDate();

    var alldays=[];
    var rows = [];
    for (var i = days; i < total+1; i++) {

        alldays.push({
            id : i,
            slots : [{
                time : "9:30 AM", available:false, id : 1
            },
            {
                time : "11:45 AM", available:true, id : 2
            },
            {
                time : "4:00 PM", available:false, id : 3
            },
            {
                time : "6:30 PM", available:true, id : 4
            },
            {
                time : "8:00 AM", available:true, id : 5
            },
            ],
            date:i+" "+curMonth+" "+curYear,
        })

        rows.push({
            day:weekday[(dayOfWeek+i-days)%7],
            curr : i,
            id : i
        })
}






        return (
            <div className="modal">
                <button className="confirm close" onClick={()=>this.props.handleClick()}>X</button>
                {/* <img src={Background} className="img" alt="cardimg"/> */}
                <img src={Img} className="img2" alt="Card Img"/>
                <div className="modal-div">
                <h3 className="month">{curMonth},{curYear}</h3>
                <div className="allmonths">
                    <span className="currmonth">December</span>
                    <span className="nextmonth">January</span>
                </div>
                <div className="alldates">
                    {
                    rows.map(each=>{
                        if(this.checkState(each.id))
                        {
                            return (
                                <div  id={each.id} className="eachDateSelected" onClick={()=>this.handleClick(each.curr)}>
                                    <span className="dayselected">{each.day}</span>
                                    <p className="dtselected">{each.curr}</p>
                                </div>
                            )
                        }
                        else
                        {
                            return (
                                <div className="eachDate" onClick={()=>this.handleClick(each.curr)}>
                                    <span className="day">{each.day}</span>
                                    <p className="dt">{each.curr}</p>
                                </div>
                            )
                        }
                    }
                        
                    )
                    }
                </div>
                    <div className="dots">
                        <div className="dot1">

                        </div>
                        <div className="dot2">

                        </div>
                    </div>
                    <div>
                        <h5 className="slot">Pick Your Slot</h5>
                    </div>
                    <div className="slots-div">

                    {
                        alldays.map(each=>{
                            if(this.checkState(each.id))
                            {

                                return (
                                    <div id={each.id} >
                                        <h5 className="slot-date">{each.date}</h5>
                                        {
                                            each.slots.map(slot=>{
                                                if(slot.available)
                                                {
                                                
                                                    if(this.state.selected === slot.id)
                                                    {
                                                        return <button id={Math.random()} className="selected" onClick={()=>this.handleSchedule(slot.id)}>{slot.time}</button>
                                                    }
                                                    else
                                                    {
                                                        return <button id={Math.random()} className="available" onClick={()=>this.handleSchedule(slot.id)}>{slot.time}</button>
                                                    }
                                                }
                                                else
                                                {
                                                    return <button id={Math.random()} className="filled">{slot.time}</button>
                                                }

                                            })
                                        }
                                    </div>
                                )
                            }
                            else
                            return null;
                        })
                    }
        </div>
        {this.state.selected!==0?
        <button className="confirm">Confirm</button>
        :
        null}
        </div>
    </div>
        )
    }



}

export default Modal;